@import '~src/css/quasar.variables.sass', 'quasar/src/css/variables.sass';

  .my_card {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: 0 auto;
    margin-bottom: 10px;
    background-color: #0053ab;
    color:white;
    padding: 0 10px;
    border-radius: 10px;

    .content_person {
      display: flex;
      justify-content: space-between;
      margin: 10px 0;
    }

    @media (min-width: $breakpoint-md-max) {
      .content_person {
        font-size: 1.4em;
        margin: 20px 0;
      }
    }

    p{
      font-size: 0.9em;
    }

    .top {
      h6 {
        margin: 0;
      }
      .percentagem {
        color: greenyellow;
        font-weight: bold;
      }
    }

    .footer {
      font-size: 0.9em;
      i {
        font-size: 30px;
      }
    }

    @media (max-width: $breakpoint-md-max) {
      .footer {
        .qtd {
          font-size: 20px;
        }
      }
    }
    @media (max-width: 305px) {
      font-size: 0.7em;
      .content_person {
        margin: 5px 0;
      }
    }
  }
